import React from "react";
import styled from "styled-components";

const TextPrincipal = styled.h3`
  color: #f7a440;
  font-size: 30px;
  margin-bottom: 20px;
  @media (max-width: 600px) {
    width: 100%;
  }
`;

export default function BtnBuscarMas(resultados) {

  return (
    <div>
      <TextPrincipal style={{ float: "left" }}>
        Mis cursos
      </TextPrincipal>
    </div>
  );
}
